*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App{
    background-color: rgb(244, 244, 150);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100%;
    button{
        transition: all 0.2s ease-in-out;
        color: black;
        border: 1px solid black;
    }
    .toggle-unit{
        width: 50px;
        height: 50px;
        position: fixed;
        top: 60px;
        right: 20px;
    }
}
.cloudy{
    background-image: url(./images/cloudy.gif);
}
.rainy{
    background-image: url(./images/rainy.gif);
    color: white;
    button{
        color: white;
        border: 1px solid white;
    }
}
.sunny{
    background-image: url(./images/sunny.gif);
    color: black;
    button{
        color: black;
        border: 1px solid black;
    }
}
.thunder{
    background-image: url(./images/thunderstorm.gif);
}
.Loading{
  height: 100vh;
  width: 100%;
  background-color: beige;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px){
    .navbar{
        height: 15vh;
        form{
            height: 50px;
            width: 70%;
            >*{
                font-size: 20px;
            }
        }
    }
}
